import { useState, useEffect, useContext } from "react";
import toast from "react-hot-toast";
import './RightPanel.scss'
import { useSearchParams } from "react-router-dom";

// import { useMediaQuery } from '@uidotdev/usehooks'
import { useChainId, useAccount, useBalance, useReadContract, useWriteContract } from "wagmi";
import { zeroAddress } from "viem";
import { ethers } from "ethers";

import 'react-tabs/style/react-tabs.scss'
import {
    TEST_NET,
    MINER_ADDRESS_TEST,
    MINER_ADDRESS_MAIN,
    MAIN_NET,
} from "../config";
import MinerAbi from "../config/abis/MINER_ABI.json";
import { RefreshContext } from '../context/RefreshContext'

import Imag1 from "../assets/images/imag1.png"

// Add animation keyframes style
const nftAnimationStyle = document.createElement('style');
nftAnimationStyle.innerHTML = `
  @keyframes pulse {
    0% { transform: scale(1); opacity: 1; }
    100% { transform: scale(1.05); opacity: 0.9; }
  }
  
  @keyframes float {
    0% { transform: translateY(0px); }
    50% { transform: translateY(-10px); }
    100% { transform: translateY(0px); }
  }
  
  @keyframes glow {
    0% { opacity: 0.5; }
    100% { opacity: 0.8; }
  }
`;
document.head.appendChild(nftAnimationStyle);

const useRefresh = () => {
    const { fast, slow } = useContext(RefreshContext)
    return { fastRefresh: fast, slowRefresh: slow }
}

export default function BuySpace() {
    // const navigate = useNavigate(); //  call useNavigate inside the component
    const [searchParams,] = useSearchParams();

    const [showModal, setShowModal] = useState(false);

    // const isMobile = useMediaQuery("only screen and (max-width: 768px)");
    const { fastRefresh } = useRefresh();

    const { writeContractAsync } = useWriteContract();

    const chainId = useChainId();
    const { isConnected, address } = useAccount();
    const { data: balance, refetch: refetchBalance } = useBalance({ address: address });

    /////// Miner //////////////////////////////////
    const { data: initialFacilityPrice, refetch: refetchInitialFacilityPrice } = useReadContract({
        address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
        abi: MinerAbi,
        functionName: "initialFacilityPrice",
        args: [],
        chainId: chainId,
    });

    /////////////////////////////////////////
    const [isPurchasingInitialFacility, setIsPurchasingInitialFacility] = useState(false);
    const [referrer, setReferrer] = useState(zeroAddress);

    useEffect(() => {
        refetchBalance();
        refetchInitialFacilityPrice();
    }, [fastRefresh]);

    useEffect(() => {
        if (searchParams.get("ref")) {
            const referrerTemp = searchParams.get("ref");
            if (referrerTemp && referrerTemp.toLowerCase() !== address?.toLowerCase())
                setReferrer(referrerTemp);
            else
                setReferrer(zeroAddress);
        }
    }, [searchParams, address])

    const handlePurchaseInitialFacility = async () => {
        setIsPurchasingInitialFacility(true);
        if (!isConnected || !address) {
            toast.error("Please connect your wallet first.");
            setIsPurchasingInitialFacility(false);
            return;
        }
        if (chainId !== TEST_NET && chainId !== MAIN_NET) {
            toast.error("Please switch to the correct network.");
            setIsPurchasingInitialFacility(false);
            return;
        }
        if (!balance || parseFloat(ethers.formatEther(balance.value)) < parseFloat(ethers.formatEther(initialFacilityPrice))) {
            toast.error("Insufficient balance to purchase the initial facility.");
            setIsPurchasingInitialFacility(false);
            return;
        }
        await writeContractAsync({
            address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
            abi: MinerAbi,
            functionName: "purchaseInitialFacility",
            args: [referrer],
            value: initialFacilityPrice
        })
            .then(() => {
                setTimeout(async () => {
                    setIsPurchasingInitialFacility(false);
                    setShowModal(false);
                }, 3000); // Keep animation running for a bit after Claim completes
            })
            .catch((err) => {
                setIsPurchasingInitialFacility(false);
            });
    }

    return (
        <div className="bg-[#0000009e] rounded-lg p-6 flex flex-col items-center justify-center space-y-6 text-center">
            <h2 className="text-white font-semibold text-3xl">
                PLEASE ACQUIRE A <br /> MINING SPACE
            </h2>
            <div className=" w-[282px] h-[252px]">
                <img src={Imag1} alt="Penguins mining" />
            </div>
            <button
                onClick={() => setShowModal(true)}
                className="bg-gradient-to-r from-yellow-500 to-orange-400 hover:opacity-90 font-bold w-[75%] py-2 text-[#78350F] text-[20px] px-6 rounded-md"
            >
                BUY SPACE
            </button>

            {/* Modal */}
            {showModal && (
                <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
                    <div className="bg-[#1E1E1E] p-6 rounded-lg w-[90%] max-w-md text-center relative border-2 border-solid border-[#F9A82C]">
                        <h2 className="text-[25px] font-bold mb-2 text-white">GET YOUR FIRST MINING SPACE</h2>
                        <p className="text-sm text-gray-300 mb-2">Start mining HASH by purchasing your first space!</p>
                        <img src="/Vector.svg" alt="Igloo" className="mx-auto my-4 w-50 h-auto" />
                        <p className="text-left text-[#F9A82C] font-medium mb-2">SET UP YOUR IGLOO</p>
                        <div className="bg-[#2B2B2B] p-4 rounded-md text-left text-sm">
                            <div className="flex justify-between mb-1 font-medium">
                                <span>- Space Capacity:</span>
                                <span className="text-yellow-400 font-medium">3 Miners</span>
                            </div>
                            <div className="flex justify-between mb-1 font-medium">
                                <span>- Price</span>
                                <span className="text-yellow-400 font-medium">{initialFacilityPrice ? ethers.formatEther(initialFacilityPrice) : 0} ETH</span>
                            </div>
                        </div>
                        <div className="flex justify-between mt-6 gap-4">
                            <button
                                onClick={() => setShowModal(false)}
                                className="w-full bg-gray-700 hover:bg-gray-600 text-white py-2 rounded-md"
                            >
                                CANCEL
                            </button>
                            <button
                                onClick={handlePurchaseInitialFacility}
                                className="w-full bg-gradient-to-r from-yellow-500 to-orange-400 hover:opacity-90 text-[#78350F] font-bold py-2 rounded-md"
                            >
                                {isPurchasingInitialFacility ? 'Buying...' : 'Buy Space'}
                            </button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    )
}
