import { useEffect, useContext, useState } from "react";
import Layout from 'layouts'
import './Dashboard.scss'
import toast from "react-hot-toast";

// import { useMediaQuery } from '@uidotdev/usehooks'
import { useChainId, useAccount, useBalance, useReadContract, useWriteContract } from "wagmi";
import { useConnectModal } from "@rainbow-me/rainbowkit";
import { zeroAddress } from "viem";
import { ethers } from "ethers";

import 'react-tabs/style/react-tabs.scss'
import {
  TEST_NET,
  HASH_ADDRESS_TEST,
  HASH_ADDRESS_MAIN,
  MINER_ADDRESS_TEST,
  MINER_ADDRESS_MAIN,
} from "../config";
import HashAbi from "../config/abis/HASH_ABI.json";
import MinerAbi from "../config/abis/MINER_ABI.json";
import { RefreshContext } from '../context/RefreshContext'

import RightPanel from "components/RigthPanel";

// Add animation keyframes style
const nftAnimationStyle = document.createElement('style');
nftAnimationStyle.innerHTML = `
  @keyframes pulse {
    0% { transform: scale(1); opacity: 1; }
    100% { transform: scale(1.05); opacity: 0.9; }
  }
  
  @keyframes float {
    0% { transform: translateY(0px); }
    50% { transform: translateY(-10px); }
    100% { transform: translateY(0px); }
  }
  
  @keyframes glow {
    0% { opacity: 0.5; }
    100% { opacity: 0.8; }
  }
`;
document.head.appendChild(nftAnimationStyle);

const useRefresh = () => {
  const { fast, slow } = useContext(RefreshContext)
  return { fastRefresh: fast, slowRefresh: slow }
}

function formatNumber(num) {
  if (num >= 1e9) return (num / 1e9).toFixed(1) + 'B';
  if (num >= 1e6) return (num / 1e6).toFixed(1) + 'M';
  if (num >= 1e3) return (num / 1e3).toFixed(1) + 'K';
  return num.toString();
}

const dailyBlocks = 86400 / 1.1

export default function BettingPage() {
  // const navigate = useNavigate(); //  call useNavigate inside the component

  // const isMobile = useMediaQuery("only screen and (max-width: 768px)");
  const { fastRefresh } = useRefresh();

  const { openConnectModal } = useConnectModal();
  const { writeContractAsync } = useWriteContract();

  const chainId = useChainId();
  const { isConnected, address } = useAccount();
  const { data: balance, refetch: refetchBalance } = useBalance({ address: address });

  ////////HASH//////////////////////////////////////
  const { data: tokenBalance, refetch: refetchTokenBalance } = useReadContract({
    address: chainId === TEST_NET ? HASH_ADDRESS_TEST : HASH_ADDRESS_MAIN,
    abi: HashAbi,
    functionName: "balanceOf",
    args: [isConnected ? address : zeroAddress],
    chainId: chainId,
  });

  /////// Miner //////////////////////////////////
  const { data: hashRate, refetch: refetchPlayerHashrate } = useReadContract({
    address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
    abi: MinerAbi,
    functionName: "playerHashrate",
    args: [isConnected ? address : zeroAddress],
    chainId: chainId,
  });

  const { data: ownerToFacility, refetch: refetchOwnerToFacility } = useReadContract({
    address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
    abi: MinerAbi,
    functionName: "ownerToFacility",
    args: [isConnected ? address : zeroAddress],
    chainId: chainId,
  });

  const { data: blocksUntilNextHalving, refetch: refetchBlocksUntilNextHalving } = useReadContract({
    address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
    abi: MinerAbi,
    functionName: "blocksUntilNextHalving",
    args: [],
    chainId: chainId,
  });

  const { data: pendingRewards, refetch: refetchPendingRewards } = useReadContract({
    address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
    abi: MinerAbi,
    functionName: "pendingRewards",
    args: [isConnected ? address : zeroAddress],
    chainId: chainId,
  });

  const { data: playerHashcoinPerBlock, refetch: refetchPlayerHashcoinPerBlock } = useReadContract({
    address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
    abi: MinerAbi,
    functionName: "playerHashcoinPerBlock",
    args: [isConnected ? address : zeroAddress],
    chainId: chainId,
  });

  const [isClaiming, setIsClaiming] = useState(false);

  useEffect(() => {
    refetchBalance();
    refetchTokenBalance();
    refetchPlayerHashrate();
    refetchOwnerToFacility();
    refetchBlocksUntilNextHalving();
    refetchPendingRewards();
    refetchPlayerHashcoinPerBlock();
  }, [fastRefresh, isConnected, address, chainId]);

  const handleClaim = async () => {
    if (!isConnected || !address) {
      toast.error("Please connect your wallet first.");
      return;
    }
    if (!pendingRewards || parseFloat(ethers.formatEther(pendingRewards)) <= 0) {
      toast.error("No rewards to claim.");
      return;
    }

    try {
      setIsClaiming(true);

      await writeContractAsync({
        address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
        abi: MinerAbi,
        functionName: "claimRewards",
        args: [],
      })
        .then(() => {
          setIsClaiming(false);
          toast.success("Rewards claimed successfully!");
        })
        .finally(() => {
          setIsClaiming(false);
        })
        .catch((err) => {
          setIsClaiming(false);
        });
    } catch (error) {
      console.error("Error claiming rewards:", error);
      toast.error("Failed to claim rewards. Please try again.");
    }
  }

  return (
    <Layout>
      {
        isConnected ?
          <>
            <div className="lg:grid px-3 lg:px-0 grid-cols-1 mx-auto relative lg:top-[50px] justify-center md:grid-cols-2 gap-6 max-w-7xl w-full">
              {/* Left Panel */}
              <div className="bg-[#0000009e] w-[100%] flex items-center justify-center  rounded-lg p-6 space-y-6">
                <div className="w-[100%]">
                  {/* Resources */}
                  <div>
                    <h2 className="text-[#F9A82C] font-semibold text-[18px]  lg:text-[22px] mb-3">Resources</h2>
                    <div className="grid grid-cols-2 gap-3 text-black font-semibold">
                      <input
                        type="text"
                        value={`${balance !== undefined ? parseFloat(ethers.formatEther(balance.value)).toFixed(3) : 0} ETH`}
                        readOnly
                        className="bg-[#282A2A] text-center border-2 border-[#F8A82C] rounded px-3 py-1 text-white"
                      />

                      <input
                        type="text"
                        value={`${ownerToFacility ? Number(ownerToFacility[1] - ownerToFacility[2]) : 0} SPACES LEFT`}
                        readOnly
                        className="bg-[#282A2A] text-center border-2 border-[#F8A82C] rounded px-3 py-1 text-white"
                      />
                      <input
                        type="text"
                        value={`${tokenBalance ? parseFloat(ethers.formatEther(tokenBalance)).toFixed(2) : 0} HASH`}
                        readOnly
                        className="bg-[#282A2A] text-center  border-2 border-[#F8A82C] rounded px-3 py-1 text-white"
                      />
                    </div>
                  </div>

                  {/* Statistics */}
                  <div>
                    <h2 className="text-[#F9A82C] font-semibold text-[20px]  lg:text-[22px] mb-3">Statistics</h2>
                    <ul className="space-y-1 pl-0">
                      <li className="flex text-[18px]  font-semibold border-b border-[#f8a62cc7] pb-2 justify-between">
                        <span>You are mining</span>
                        <span className="text-[#FFD100]">{`${playerHashcoinPerBlock ? formatNumber(parseFloat(ethers.formatEther(playerHashcoinPerBlock)) * dailyBlocks) : `--`} HASH / DAY`}</span>
                      </li>
                      <li className="flex text-[18px]  font-semibold border-b border-[#f8a62cc7] pb-2 justify-between">
                        <span>Your hash rate is</span>
                        <span lassName="text-[#FFD100]">{hashRate ? Number(hashRate) : `--`} GH / S</span>
                      </li>
                      <li className="flex text-[18px]  font-semibold border-b border-[#f8a62cc7] pb-2 justify-between">
                        <span>Blocks until next halvening</span>
                        <span lassName="text-[#FFD100]">{`${blocksUntilNextHalving ? blocksUntilNextHalving : `--`} BLOCKS`}</span>
                      </li>
                    </ul>
                  </div>

                  {/* Claim Button */}
                  <div className="bg-gray-800 rounded-lg p-4 text-center">
                    <p className="text-white font-semibold lg:text-2xl text-lg mb-3">
                      You Have Mined : <span className="text-[#F8A82C]">{`${pendingRewards ? parseFloat(ethers.formatEther(pendingRewards)).toFixed(2) : `0`} $HASH`}</span>
                    </p>
                    <button className="bg-gradient-to-r from-yellow-500 to-orange-400 hover:opacity-90 font-bold w-full py-1 text-[#78350F] text-[16px] px-6 rounded-md" onClick={handleClaim}>
                      { isClaiming ? "Claiming..." : "Claim Now" }
                    </button>
                  </div>
                </div>
              </div>

              <RightPanel />
            </div>
            <div className="relative mt-[92px] mb-[40px]">
              <div className="flex justify-center flex-wrap gap-3">
                {/* Twitter */}
                <a
                  href="https://twitter.com/hashlordz"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="flex  items-center gap-3 no-underline bg-[#0000009e] w-[200px] hover:bg-[#2a2c2f] rounded-lg px-4 py-2 transition duration-200"
                >
                  <svg width="33" height="33" className='block' viewBox="0 0 33 33" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M32.492 16.2449C32.492 7.27199 25.218 -0.00201416 16.245 -0.00201416C7.27208 -0.00201416 -0.00195312 7.27199 -0.00195312 16.2449C-0.00195312 25.2179 7.27208 32.4919 16.245 32.4919C25.218 32.4919 32.492 25.2179 32.492 16.2449Z" fill="#0EA5E9" fill-opacity="0.3" />
                    <path d="M23.059 11.777C22.572 12.021 22.084 12.102 21.516 12.183C22.084 11.858 22.49 11.371 22.653 10.721C22.166 11.046 21.597 11.208 20.947 11.371C20.46 10.884 19.728 10.559 18.997 10.559C17.291 10.559 15.992 12.183 16.398 13.808C14.205 13.727 12.255 12.671 10.874 11.046C10.143 12.265 10.549 13.808 11.686 14.62C11.28 14.62 10.874 14.458 10.468 14.295C10.468 15.514 11.361 16.651 12.58 16.976C12.174 17.057 11.767 17.139 11.361 17.057C11.686 18.113 12.661 18.926 13.88 18.926C12.905 19.657 11.443 20.063 10.062 19.901C11.28 20.632 12.661 21.119 14.123 21.119C19.079 21.119 21.841 16.976 21.678 13.158C22.247 12.833 22.734 12.346 23.059 11.777Z" fill="#73D5FF" />
                  </svg>
                  <div className="text-white">
                    <p className="text-sm mb-0 font-semibold">Twitter</p>
                    <p className="text-xs mb-0 text-gray-400">@hashlordz</p>
                  </div>
                </a>

                {/* Discord */}
                <a
                  href="https://discord.gg/hashlordz"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="flex  items-center gap-3 no-underline bg-[#0000009e] w-[200px] hover:bg-[#2a2c2f] rounded-lg px-4 py-3 transition duration-200"
                >
                  <svg width="33" height="33" viewBox="0 0 33 33" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M32.492 16.2449C32.492 7.27199 25.218 -0.00201416 16.245 -0.00201416C7.27208 -0.00201416 -0.00195312 7.27199 -0.00195312 16.2449C-0.00195312 25.2179 7.27208 32.4919 16.245 32.4919C25.218 32.4919 32.492 25.2179 32.492 16.2449Z" fill="#0EA5E9" fill-opacity="0.3" />
                    <path d="M23.059 11.777C22.572 12.021 22.084 12.102 21.516 12.183C22.084 11.858 22.49 11.371 22.653 10.721C22.166 11.046 21.597 11.208 20.947 11.371C20.46 10.884 19.728 10.559 18.997 10.559C17.291 10.559 15.992 12.183 16.398 13.808C14.205 13.727 12.255 12.671 10.874 11.046C10.143 12.265 10.549 13.808 11.686 14.62C11.28 14.62 10.874 14.458 10.468 14.295C10.468 15.514 11.361 16.651 12.58 16.976C12.174 17.057 11.767 17.139 11.361 17.057C11.686 18.113 12.661 18.926 13.88 18.926C12.905 19.657 11.443 20.063 10.062 19.901C11.28 20.632 12.661 21.119 14.123 21.119C19.079 21.119 21.841 16.976 21.678 13.158C22.247 12.833 22.734 12.346 23.059 11.777Z" fill="#73D5FF" />
                  </svg>
                  <div className="text-white">
                    <p className="text-sm mb-0 font-semibold">Discord</p>
                    <p className="text-xs mb-0 text-gray-400">discord.gg/hashlordz</p>
                  </div>
                </a>

                {/* Telegram */}
                <a
                  href="https://t.me/hashlordz"
                  target="_blank"
                  rel="noopener noreferrer"
                  className="flex  items-center gap-3 no-underline bg-[#0000009e] w-[200px] hover:bg-[#2a2c2f] rounded-lg px-4 py-2 transition duration-200"
                >
                  <svg width="34" height="33" viewBox="0 0 34 33" fill="none" xmlns="http://www.w3.org/2000/svg">
                    <path d="M33.1689 16.2449C33.1689 7.27199 25.8949 -0.00201416 16.9219 -0.00201416C7.94899 -0.00201416 0.674988 7.27199 0.674988 16.2449C0.674988 25.2179 7.94899 32.4919 16.9219 32.4919C25.8949 32.4919 33.1689 25.2179 33.1689 16.2449Z" fill="#3B82F6" fill-opacity="0.3" />
                    <path d="M22.187 15.839V19.901C22.186 20.439 21.972 20.955 21.591 21.335C21.211 21.716 20.695 21.93 20.157 21.932H17.237C17.129 21.932 17.026 21.889 16.95 21.813C16.874 21.736 16.831 21.633 16.831 21.525C16.831 21.418 16.874 21.314 16.95 21.238C17.026 21.162 17.129 21.119 17.237 21.119H20.157C20.479 21.118 20.789 20.989 21.017 20.761C21.245 20.533 21.374 20.223 21.375 19.901V19.83C21.245 19.877 21.107 19.901 20.969 19.901H20.157C19.833 19.901 19.523 19.772 19.295 19.544C19.066 19.315 18.938 19.005 18.938 18.682V16.651C18.938 16.328 19.066 16.018 19.295 15.79C19.523 15.561 19.833 15.433 20.157 15.433H21.355C21.253 14.317 20.736 13.28 19.905 12.528C19.075 11.775 17.992 11.363 16.872 11.371H16.867C15.745 11.36 14.661 11.772 13.831 12.525C13 13.278 12.483 14.316 12.383 15.433H13.582C13.904 15.434 14.214 15.563 14.442 15.791C14.67 16.019 14.799 16.329 14.8 16.651V18.682C14.799 19.005 14.67 19.314 14.442 19.542C14.214 19.771 13.904 19.899 13.582 19.901H12.769C12.447 19.899 12.137 19.771 11.909 19.542C11.681 19.314 11.552 19.005 11.551 18.682V15.839C11.551 15.143 11.689 14.453 11.956 13.81C12.224 13.167 12.616 12.583 13.11 12.093C13.604 11.602 14.19 11.214 14.835 10.95C15.48 10.687 16.17 10.554 16.867 10.559H16.907C18.307 10.56 19.649 11.117 20.639 12.107C21.629 13.097 22.186 14.439 22.187 15.839Z" fill="#3B82F6" />
                  </svg>
                  <div className="text-white">
                    <p className="text-sm mb-0 font-semibold">Telegram</p>
                    <p className="text-xs mb-0 text-gray-400">t.me/hashlordz</p>
                  </div>
                </a>
              </div>
            </div>
          </>
          :
          <>
            <div className="flex items-center justify-center h-screen">
              <div className="bg-[#0000009e] rounded-lg p-6 text-center">
                <h2 className="text-white font-semibold text-3xl mb-4">Please Connect Your Wallet</h2>
                <p className="text-gray-400 mb-6 text-[20px]">You need to connect your wallet to start mining.</p>
                <button
                  onClick={openConnectModal}
                  className="bg-gradient-to-r from-yellow-500 to-orange-400 hover:opacity-90 font-bold w-full py-2 text-[#78350F] text-[20px] px-6 rounded-md"
                >
                  Connect Wallet
                </button>
              </div>
            </div>
          </>
      }

    </Layout>
  )
}
