"use client"

import { useEffect, useState } from "react"
import { useChainId, useAccount, useReadContract, useWriteContract } from "wagmi";
import { zeroAddress } from "viem";
import { ethers } from "ethers";

import {
    TEST_NET,
    MINER_ADDRESS_TEST,
    MINER_ADDRESS_MAIN,
} from "../config";
import MinerAbi from "../config/abis/MINER_ABI.json";

import ImgLeaf from '../assets/images/miners/leaf.png'
import ImgSteel from '../assets/images/miners/steel.png'
import ImgDiamond from '../assets/images/miners/diamond.png'
import '../index.css'

const ary = Array.from({ length: 10000 }, (_, i) => ({
    isCancelling: false,
    isBuying: false
}));

export default function Marketplace() {
    const chainId = useChainId();
    const { isConnected, address } = useAccount();
    const { writeContractAsync } = useWriteContract();

    const { data: playerMinersOwned, refetch: refetchPlayerMinersOwned } = useReadContract({
        address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
        abi: MinerAbi,
        functionName: "getMinersOnMarketplace",
        args: [],
        chainId: chainId,
    });

    const [selfMiners, setSelfMiners] = useState([]);
    const [otherMiners, setOtherMiners] = useState([]);

    const [btnStatus, setBtnStatus] = useState(ary);

    useEffect(() => {
        if (address === undefined || address === zeroAddress || !isConnected || playerMinersOwned === undefined || chainId === undefined)
            return;

        let selfMinersTemp = [];
        let otherMinersTemp = [];
        playerMinersOwned.forEach((item) => {
            if (item.owner.toLowerCase() === address.toLowerCase()) {
                selfMinersTemp.push(item);
            } else {
                otherMinersTemp.push(item);
            }
        });

        setSelfMiners(selfMinersTemp);
        setOtherMiners(otherMinersTemp);

    }, [address, playerMinersOwned, isConnected, chainId]);

    const handleCancelListing = (id, index) => async () => {
        if (address === undefined || address === zeroAddress || !isConnected || chainId === undefined) {
            return;
        }

        let temp = btnStatus;
        temp[index].isCancelling = true;
        setBtnStatus(temp);

        await writeContractAsync({
            address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
            abi: MinerAbi,
            functionName: "cancelListForSale",
            args: [id],
        })
            .then(() => {
                temp[index].isCancelling = false;
                setBtnStatus(temp);
            })
            .finally(() => {
                temp[index].isCancelling = false;
                setBtnStatus(temp);
            })
            .catch((err) => {
                temp[index].isCancelling = false;
                setBtnStatus(temp);
            });
    }

    const handleBuying = (id, price, index) => async () => {
        if (address === undefined || address === zeroAddress || !isConnected || chainId === undefined) {
            return;
        }

        let temp = btnStatus;
        temp[index].isBuying = true;
        setBtnStatus(temp);

        await writeContractAsync({
            address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
            abi: MinerAbi,
            functionName: "buyMinerFromMarket",
            args: [id],
            value: price,
        })
            .then(() => {
                temp[index].isBuying = false;
                setBtnStatus(temp);
            })
            .finally(() => {
                temp[index].isBuying = false;
                setBtnStatus(temp);
            })
            .catch((err) => {
                temp[index].isBuying = false;
                setBtnStatus(temp);
            });
    }


    return (
        <div div className="p-2">
            <div className="pt-4 pb-2 px-2 text-white"> Your miners that listed for sale.</div>
            <div className="overflow-x-auto">
                <table className="min-w-full divide-y divide-gray-200 border rounded-lg shadow-sm">
                    <thead className="bg-black-100">
                        <tr>
                            <th className="px-1 y-2 text-left">Miner</th>
                            <th className="px-1 py-2 text-right">Type</th>
                            <th className="px-1 py-2 text-right">Level</th>
                            <th className="px-1 py-2 text-right">Cost</th>
                            <th className="px-1 py-2 text-center">Action</th>
                        </tr>
                    </thead>
                    <tbody className="divide-y divide-gray-200">
                        {
                            selfMiners !== undefined && selfMiners.length !== 0 ?
                                selfMiners.map((item, index) => {
                                    const image = Number(item.minerIndex) === 1 ? ImgLeaf : (Number(item.minerIndex) === 2 ? ImgSteel : ImgDiamond);
                                    const minerType = Number(item.minerIndex) === 1 ? "Leaf" : (Number(item.minerIndex) === 2 ? "Steel" : "Diamond");
                                    return (
                                        <tr key={item.id} className="hover:bg-blue-500">
                                            <td className="px-1 py-2">
                                                <img src={image} alt="Item" className="h-10 rounded-full" />
                                            </td>
                                            <td className="px-1 py-2 text-right">{minerType}</td>
                                            <td className="px-1 py-2 text-right">{Number(item.level)}</td>
                                            <td className="px-1 py-2 text-right">{ethers.formatEther(item.sellPrice)}</td>
                                            <td className="px-1 py-2 text-center">
                                                <button className="px-1 py-1 text-sm font-medium text-white bg-blue-600 rounded hover:bg-blue-700" onClick={handleCancelListing(item.id, index)}>
                                                    {btnStatus[index].isCancelling ? "Cancelling..." : "Cancel"}
                                                </button>
                                            </td>
                                        </tr>
                                    )
                                })
                                :
                                <div className="px-1 py-2 text-red-500">There is no any miner you listed!</div>
                        }
                    </tbody>
                </table>
            </div>

            <div className="pt-4 pb-2 px-2 text-white"> Miners that you can buy.</div>
            <div className="overflow-x-auto">
                <table className="min-w-full divide-y divide-gray-200 border rounded-lg shadow-sm">
                    <thead className="bg-black-100">
                        <tr>
                            <th className="px-1 py-2 text-left">Miner</th>
                            <th className="px-1 py-2 text-right">Type</th>
                            <th className="px-1 py-2 text-right">Level</th>
                            <th className="px-1 py-2 text-right">Cost</th>
                            <th className="px-1 py-2 text-center">Action</th>
                        </tr>
                    </thead>
                    <tbody className="divide-y divide-gray-200">
                        {
                            otherMiners !== undefined && otherMiners.length !== 0 ?
                                otherMiners?.map((item, index) => {
                                    const image = Number(item.minerIndex) === 1 ? ImgLeaf : (Number(item.minerIndex) === 2 ? ImgSteel : ImgDiamond);
                                    const minerType = Number(item.minerIndex) === 1 ? "Leaf" : (Number(item.minerIndex) === 2 ? "Steel" : "Diamond");
                                    return (
                                        <tr key={item.id} className="hover:bg-blue-500">
                                            <td className="px-1 py-2">
                                                <img src={image} alt="Item" className="h-10 rounded-full" />
                                            </td>
                                            <td className="px-1 py-2 text-right">{minerType}</td>
                                            <td className="px-1 py-2 text-right">{Number(item.level)}</td>
                                            <td className="px-1 py-2 text-right">{ethers.formatEther(item.sellPrice)}</td>
                                            <td className="px-1 py-2 text-center">
                                                <button className="px-1 py-1 text-sm font-medium text-white bg-blue-600 rounded hover:bg-blue-700" onClick={handleBuying(item.id, item.sellPrice, index)}>
                                                    {btnStatus[index].isBuying ? "Buying..." : "Buy"}
                                                </button>
                                            </td>
                                        </tr>
                                    )
                                })
                                :
                                <div className="px-1 py-2 text-red-500">There is no any miner you can buy!</div>
                        }
                    </tbody>
                </table>
            </div>
        </div>
    )
}
