"use client"

import { useState } from "react"
import LeftArrow from '../assets/images/arrow-left.svg'
import RightArrow from '../assets/images/arrow-right.svg'
import ImgHumar1 from '../assets/images/miners/leaf.png'
import ImgHumar2 from '../assets/images/miners/steel.png'
import ImgHumar3 from '../assets/images/miners/diamond.png'
import '../index.css'
import { useChainId, useAccount, useBalance, useReadContract, useWriteContract, useDisconnect } from "wagmi";
import { useConnectModal } from "@rainbow-me/rainbowkit";
import {
    MINER_ADDRESS_MAIN,
    MINER_ADDRESS_TEST,
    HASH_ADDRESS_TEST,
    HASH_ADDRESS_MAIN,
    TEST_NET,
    MAIN_NET,
    UNIT256_MAX
} from "../config"
import HashAbi from "../config/abis/HASH_ABI.json";
import MinerAbi from "../config/abis/MINER_ABI.json";
import { zeroAddress } from "viem";
import toast from "react-hot-toast";
import { BaseWallet, ethers } from "ethers";

export default function Space() {
    const [activeItem, setActiveItem] = useState()
    const [selectedItem, setSelectedItem] = useState(-1)

    const [price, setPrice] = useState(0);

    const [isPurchasingMiner, setIsPurchasingMiner] = useState(false);
    const [isUpgradingMiner, setIsUpgradingMiner] = useState(false);
    const [isListingMiner, setIsListingMiner] = useState(false);

    const [showModal, setShowModal] = useState(false);
    const [showSellModal, setShowSellModal] = useState(false);

    const { data: txResult, writeContractAsync } = useWriteContract();

    const { openConnectModal } = useConnectModal();
    const { disconnect } = useDisconnect();

    const chainId = useChainId();
    const { isConnected, address } = useAccount();
    const { data: balance, refetch: refetchBalance } = useBalance({ address: address });

    ////////HASH//////////////////////////////////////
    const { data: tokenBalance, refetch: refetchTokenBalance } = useReadContract({
        address: chainId === TEST_NET ? HASH_ADDRESS_TEST : HASH_ADDRESS_MAIN,
        abi: HashAbi,
        functionName: "balanceOf",
        args: [isConnected ? address : zeroAddress],
        chainId: chainId,
    });

    const { data: approvedTokenAmount, refetch: refetchApprovedTokenAmount } = useReadContract({
        address: chainId === TEST_NET ? HASH_ADDRESS_TEST : HASH_ADDRESS_MAIN,
        abi: HashAbi,
        functionName: "allowance",
        args: [isConnected ? address : zeroAddress, chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN],
        chainId: chainId,
    });

    /////// Miner //////////////////////////////////
    const { data: playerMinersPaginated, refetch: refetchPlayerMinersPaginated } = useReadContract({
        address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
        abi: MinerAbi,
        functionName: "getPlayerMinersPaginated",
        args: [isConnected ? address : zeroAddress, 0, isConnected ? 100 : 0],
        chainId: chainId,
    });

    const { data: ownerToFacility, refetch: refetchOwnerToFacility } = useReadContract({
        address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
        abi: MinerAbi,
        functionName: "ownerToFacility",
        args: [isConnected ? address : zeroAddress],
        chainId: chainId,
    });

    const { data: leafCost, refetch: refetchLeafCost } = useReadContract({
        address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
        abi: MinerAbi,
        functionName: "getTokenAmountFromEthAmount",
        args: ['2500000000'],
        chainId: chainId,
    });
    const { data: steelCost, refetch: refetchSteelCost } = useReadContract({
        address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
        abi: MinerAbi,
        functionName: "getTokenAmountFromEthAmount",
        args: ['2500000000'],
        chainId: chainId,
    });
    const { data: diamondCost, refetch: refetchDiamondCost } = useReadContract({
        address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
        abi: MinerAbi,
        functionName: "getTokenAmountFromEthAmount",
        args: ['250000000000'],
        chainId: chainId,
    });
    const { data: timeUntilNextFacilityUpgrade, refetch: refetchTimeUntilNextFacilityUpgrade } = useReadContract({
        address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
        abi: MinerAbi,
        functionName: "timeUntilNextFacilityUpgrade",
        args: [isConnected ? address : zeroAddress],
        chainId: chainId,
    });

    const availableMiners = !(playerMinersPaginated && playerMinersPaginated?.length > 0) ? [] : playerMinersPaginated.filter(item => Number(item.x) === 100);

    const handleBuyMiner = async (minerIndex) => {
        setIsPurchasingMiner(true);
        if (!isConnected || !address) {
            toast.error("Please connect your wallet first.");
            setIsPurchasingMiner(false);
            return;
        }
        if (chainId !== TEST_NET && chainId !== MAIN_NET) {
            toast.error("Please switch to the correct network.");
            setIsPurchasingMiner(false);
            return;
        }
        if (Number(timeUntilNextFacilityUpgrade) > 0) {
            toast.error(`Please wait cooldown ${timeUntilNextFacilityUpgrade} seconds`);
            setIsPurchasingMiner(false);
            return;
        }
        if (!balance || parseFloat(ethers.formatEther(balance.value)) < 0.0001) {
            toast.error("Insufficient gas fee.");
            setIsPurchasingMiner(false);
            return;
        }
        const tokenAmount = minerIndex === 1 ? ethers.formatEther(leafCost) : minerIndex === 2 ? ethers.formatEther(steelCost) : ethers.formatEther(diamondCost);
        if (!tokenAmount || parseFloat(ethers.formatEther(tokenBalance)) < parseFloat(tokenAmount)) {
            toast.error("Insufficient token amount.");
            setIsPurchasingMiner(false);
            return;
        }
        if (!tokenAmount || parseFloat(ethers.formatEther(approvedTokenAmount)) < parseFloat(tokenAmount)) {
            await writeContractAsync({
                address: chainId === TEST_NET ? HASH_ADDRESS_TEST : HASH_ADDRESS_MAIN,
                abi: MinerAbi,
                functionName: "approve",
                args: [chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN, UNIT256_MAX],
                value: '0'
            })
                .then(() => {
                    // setTimeout(async () => {
                    //   setIsPurchasingMiner(false);
                    //   setShowModal(false);
                    //   await refetchPlayerMinersPaginated();
                    //   await refetchOwnerToFacility();
                    // }, 3000); // Keep animation running for a bit after Claim completes
                })
                .catch((err) => {
                    setIsPurchasingMiner(false);
                });
        }
        await writeContractAsync({
            address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
            abi: MinerAbi,
            functionName: "buyMiner",
            args: [minerIndex],
            value: '0'
        })
            .then(() => {
                setTimeout(async () => {
                    setIsPurchasingMiner(false);
                    setShowModal(false);
                    await refetchPlayerMinersPaginated();
                    await refetchOwnerToFacility();
                }, 3000); // Keep animation running for a bit after Claim completes
            })
            .catch((err) => {
                setIsPurchasingMiner(false);
            });
    }

    const handleSellMiner = async () => {
        setIsListingMiner(true);
        if (!isConnected || !address) {
            toast.error("Please connect your wallet first.");
            setIsListingMiner(false);
            return;
        }
        if (chainId !== TEST_NET && chainId !== MAIN_NET) {
            toast.error("Please switch to the correct network.");
            setIsListingMiner(false);
            return;
        }
        if (!balance || parseFloat(ethers.formatEther(balance.value)) < 0.0001) {
            toast.error("Insufficient gas fee.");
            setIsListingMiner(false);
            return;
        }
        if (price <= 0) {
            toast.error("Please input valid price.");
            setIsListingMiner(false);
            return;
        }
        await writeContractAsync({
            address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
            abi: MinerAbi,
            functionName: "listForSale",
            args: [activeItem?.id, ethers.parseEther(price)],
            value: '0'
        })
            .then(() => {
                setTimeout(async () => {
                    setIsListingMiner(false);
                    setShowSellModal(false);
                    await refetchPlayerMinersPaginated();
                    await refetchOwnerToFacility();
                }, 3000); // Keep animation running for a bit after Claim completes
            })
            .catch((err) => {
                setIsListingMiner(false);
                console.log(err);
            });
    }

    const handleUpgradeLevel = async (minerId) => {
        setIsUpgradingMiner(true);
        if (!isConnected || !address) {
            toast.error("Please connect your wallet first.");
            setIsUpgradingMiner(false);
            return;
        }
        if (chainId !== TEST_NET && chainId !== MAIN_NET) {
            toast.error("Please switch to the correct network.");
            setIsUpgradingMiner(false);
            return;
        }
        const miner = availableMiners.find(i => i.id.toString() === minerId.toString());
        if (miner.level.toString() === "5") {
            toast.error("Miner is already at max level.");
            setIsUpgradingMiner(false);
            return;
        }
        if (Number(timeUntilNextFacilityUpgrade) > 0) {
            toast.error(`Please wait cooldown ${timeUntilNextFacilityUpgrade} seconds`);
            setIsUpgradingMiner(false);
            return;
        }
        if (!balance || parseFloat(ethers.formatEther(balance.value)) < 0.0001) {
            toast.error("Insufficient gas fee.");
            setIsUpgradingMiner(false);
            return;
        }
        const tokenAmount = minerId === "3" ? ethers.formatEther(diamondCost) : minerId === "2" ? ethers.formatEther(steelCost) : ethers.formatEther(leafCost);
        if (!tokenAmount || parseFloat(ethers.formatEther(tokenBalance)) < parseFloat(tokenAmount)) {
            toast.error("Insufficient token amount.");
            setIsUpgradingMiner(false);
            return;
        }
        if (!tokenAmount || parseFloat(ethers.formatEther(approvedTokenAmount)) < parseFloat(tokenAmount)) {
            await writeContractAsync({
                address: chainId === TEST_NET ? HASH_ADDRESS_TEST : HASH_ADDRESS_MAIN,
                abi: MinerAbi,
                functionName: "approve",
                args: [chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN, UNIT256_MAX],
                value: '0'
            })
                .then(() => {
                    // setTimeout(async () => {
                    //   setIsUpgradingMiner(false);
                    //   setShowModal(false);
                    //   await refetchPlayerMinersPaginated();
                    //   await refetchOwnerToFacility();
                    // }, 3000); // Keep animation running for a bit after Claim completes
                })
                .catch((err) => {
                    setIsUpgradingMiner(false);
                });
        }
        await writeContractAsync({
            address: chainId === TEST_NET ? MINER_ADDRESS_TEST : MINER_ADDRESS_MAIN,
            abi: MinerAbi,
            functionName: "updateMiner",
            args: [minerId],
            value: '0'
        })
            .then(() => {
                setTimeout(async () => {
                    setIsUpgradingMiner(false);
                    setShowModal(false);
                    await refetchPlayerMinersPaginated();
                    await refetchOwnerToFacility();
                }, 3000); // Keep animation running for a bit after Claim completes
            })
            .catch((err) => {
                setIsUpgradingMiner(false);
            });
    }

    return (
        <div className="col-span-2 rounded-b-lg space-y-6 p-4 bg-[#1B2531]">
            <div className="flex justify-between">
                <div className="space-y-2 w-1/2 text-xl">
                    <div className="text-yellow-400 font-medium">ID: #{activeItem?.id.toString() ?? "-"}</div>
                    <div className="text-yellow-400 font-medium">Level: {activeItem?.level.toString() ?? "-"}</div>
                    <div className="text-yellow-400 font-medium" onClick={() => console.log(activeItem)}>Power: {activeItem?.hashrate.toString() ?? "-"}</div>
                </div>
                {activeItem?.id.toString() ? <div className="bg-black w-1/2 rounded-lg border border-slate-600 p-2 aspect-square max-w-28 max-h-28">
                    <div className="relative w-full h-full">
                        <div className="absolute top-1 left-1 text-xs text-yellow-400 font-bold">#{activeItem?.id.toString() ?? ""}</div>
                        <div className="absolute top-1 right-1 text-xs text-white bg-slate-700/80 px-1.5 py-0.5 rounded">Lv {activeItem?.level.toString() ?? ""}</div>
                        <div className="flex items-center justify-center h-full">
                            <div className="relative w-full h-full flex items-center justify-center">
                                <img src={activeItem?.minerIndex.toString() === "1" ? ImgHumar1 : activeItem?.minerIndex.toString() === "2" ? ImgHumar2 : ImgHumar3} alt="miner" />
                            </div>
                        </div>
                    </div>
                </div> : <></>}
            </div>

            {/* <div className="border-t border-slate-700/50"></div> */}

            {/* 
      <div className="space-y-3">
        <h3 className="text-white font-medium text-xl">Move Miner</h3>
        <div className="flex items-center space-x-4">
          <span className="text-white text-xl mr-3">A</span>
          <div className="flex items-center justify-center">
            <img src={LeftArrow} alt="" className="w-[25px]" />
            <img src={RightArrow} alt="" className="w-[25px]" />
          </div>
          <span className="text-white text-xl ml-3">B</span>
          <button className="px-3 py-1 text-xl text-white font-medium">SWAP</button>
        </div>
      </div> */}

            <div className="border-t border-slate-700/50"></div>

            {/* <div className="space-y-3">
        <h3 className="text-yellow-400 font-medium text-xl">Sell Miner</h3>
        <div className="flex space-x-2">
          <div className="relative flex-1">
            <input
              type="text"
              placeholder="Price"
              className="w-full px-3 text-xl py-2 bg-[#15120D] border-2 border-yellow-500 rounded-lg text-white placeholder-slate-400 focus:outline-none focus:border-yellow-400 pr-12"
            />
            <span className="absolute right-3 text-xl top-1/2 transform -translate-y-1/2 text-yellow-400 font-medium">ETH</span>
          </div>
          <button className="px-4 py-2 text-yellow-500 border-2 border-yellow-500 rounded-lg text-sm font-medium">LIST</button>
        </div>
      </div> */}

            <div className="grid grid-cols-3 gap-3 mt-6 h-[400px] overflow-y-auto p-3">
                {[null, ...availableMiners].map((item, index) => (
                    <div
                        key={index}
                        onClick={(event) => {
                            setSelectedItem(index);
                            // console.log('selectedItem', Number(ownerToFacility[1]));
                            if (!item) {
                                setShowModal(true);
                            } else {
                                setActiveItem(availableMiners[index - 1])
                            }
                        }}
                        className={`relative aspect-square flex items-center justify-center bg-black rounded-lg  cursor-pointer transition-all ${index === selectedItem ? "border-yellow-400 shadow-lg shadow-yellow-400/20 border-4" : "border-white border-2"}`}
                    >
                        {item ? (<>
                            <div className="absolute top-1 left-1 text-xs text-white font-bold">#{item?.id.toString()}</div>
                            <div className="absolute top-1 right-1 text-xs text-white bg-slate-700/80 px-1.5 py-0.5 rounded">Lv {item?.level.toString()}</div>
                            <div className="p-2 h-full">
                                <div className="relative w-full h-full flex items-center justify-center">
                                    <img src={item?.minerIndex.toString() === "1" ? ImgHumar1 : item?.minerIndex.toString() === "2" ? ImgHumar2 : ImgHumar3} alt="miner" />
                                </div>
                            </div>
                            <button
                                className="absolute bottom-8 mx-auto text-xs text-white bg-slate-700/80 px-1.5 py-0.5 rounded h-6 w-32"
                                onClick={(event) => {
                                    event.stopPropagation();
                                    setSelectedItem(index)
                                    setActiveItem(availableMiners[index - 1])
                                    setShowSellModal(true);
                                }}
                            >{isListingMiner ? 'Listing...' : 'List Miner'}</button>
                            <button
                                className="absolute bottom-1 mx-auto text-xs text-white bg-slate-700/80 px-1.5 py-0.5 rounded h-6 w-32"
                                onClick={(event) => {
                                    event.stopPropagation();
                                    setActiveItem(availableMiners[index - 1])
                                    setSelectedItem(index)
                                    handleUpgradeLevel(item?.id.toString())
                                }}
                            >{isUpgradingMiner ? 'Updating...' : 'Update Level'}</button>
                        </>) : (
                            <h2 className="text-slate-600 pb-[7px] lg:pb-0 h-fit lg:text-2xl mb-0 text-[20px] font-black">Buy Miner</h2>
                        )}
                    </div>
                ))}
            </div>

            {/* Modal */}
            {showModal && (
                <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
                    <div className="bg-[#1E1E1E] p-6 rounded-lg w-[90%] max-w-md text-center relative border-2 border-solid border-[#F9A82C]">
                        <h2 className="text-[25px] font-bold mb-2 text-white">BUY MINER</h2>
                        <p className="text-sm text-gray-300 mb-2">Buy more miners!</p>
                        <img src="/Vector.svg" alt="Igloo" className="mx-auto my-4 w-50 h-auto" />
                        <p className="text-left text-[#F9A82C] font-medium mb-2">Miners</p>
                        <div className="bg-[#2B2B2B] p-4 rounded-md text-left text-sm">
                            <div className="flex justify-between mb-1 font-medium">
                                <span>- Available</span>
                                <span className="text-yellow-400 font-medium">{`${ownerToFacility ? Number(ownerToFacility[1]) - Number(ownerToFacility[2]) : `-`} Miners`}</span>
                            </div>
                            <div className="flex justify-between mb-1 font-medium">
                                <span>- Leaf Miner</span>
                                <span className="text-yellow-400 font-medium">{`${leafCost ? ethers.formatEther(leafCost) : `-`} $Hash`}</span>
                            </div>
                            {Number(ownerToFacility[1]) > 3 ? <div className="flex justify-between mb-1 font-medium">
                                <span>- Steel Miner</span>
                                <span className="text-yellow-400 font-medium">{`${steelCost ? ethers.formatEther(steelCost) : `-`} $Hash`}</span>
                            </div> : <></>}
                            {Number(ownerToFacility[1]) > 5 ? <div className="flex justify-between mb-1 font-medium">
                                <span>- Diamond Miner</span>
                                <span className="text-yellow-400 font-medium">{`${diamondCost ? ethers.formatEther(diamondCost) : `-`} $Hash`}</span>
                            </div> : <></>}
                        </div>
                        <div className="grid grid-cols-2 gap-3 mt-6">
                            <button
                                onClick={() => setShowModal(false)}
                                className="w-full bg-gray-700 hover:bg-gray-600 text-white py-2 rounded-md"
                            >
                                CANCEL
                            </button>
                            <button
                                onClick={() => handleBuyMiner(1)}
                                className="w-full bg-gradient-to-r from-yellow-500 to-orange-400 hover:opacity-90 text-[#78350F] font-bold py-2 rounded-md"
                            >
                                {isPurchasingMiner ? 'Purchasing...' : 'Buy Leaf Miner'}
                            </button>
                            {Number(ownerToFacility[1]) > 3 ? <button
                                onClick={() => handleBuyMiner(2)}
                                className="w-full bg-gradient-to-r from-yellow-500 to-orange-400 hover:opacity-90 text-[#78350F] font-bold py-2 rounded-md"
                            >
                                {isPurchasingMiner ? 'Purchasing...' : 'Buy Steel Miner'}
                            </button> : <></>}
                            {Number(ownerToFacility[1]) > 5 ? <button
                                onClick={() => handleBuyMiner(3)}
                                className="w-full bg-gradient-to-r from-yellow-500 to-orange-400 hover:opacity-90 text-[#78350F] font-bold py-2 rounded-md"
                            >
                                {isPurchasingMiner ? 'Purchasing...' : 'Buy Diamond Miner'}
                            </button> : <></>}
                        </div>
                    </div>
                </div>
            )}

            {/* Modal */}
            {showSellModal && (
                <div className="fixed inset-0 bg-black bg-opacity-50 flex items-center justify-center z-50">
                    <div className="bg-[#1E1E1E] p-6 rounded-lg w-[90%] max-w-md text-center relative border-2 border-solid border-[#F9A82C]">
                        <h2 className="text-[25px] font-bold mb-2 text-white">Sell Miner</h2>
                        <p className="text-sm text-gray-300 mb-2">List your miner to sell!</p>
                        <img src="/Vector.svg" alt="Igloo" className="mx-auto my-4 w-50 h-auto" />
                        <p className="text-left text-[#F9A82C] font-medium mb-2">Miners</p>
                        <div className="bg-[#2B2B2B] p-4 rounded-md text-left text-sm">
                            <div className="relative flex-1">
                                <input
                                    type="text"
                                    placeholder="Price"
                                    className="w-full px-3 text-xl py-2 bg-[#15120D] border-2 border-yellow-500 rounded-lg text-white placeholder-slate-400 focus:outline-none focus:border-yellow-400 pr-12"
                                    onChange={(e) => {
                                        const value = e.target.value;
                                        if (!isNaN(value) && value >= 0) {
                                            setPrice(value);
                                        } else {
                                            toast.error("Please enter a valid price.");
                                        }
                                    }}
                                />
                                <span className="absolute right-3 text-xl top-1/2 transform -translate-y-1/2 text-yellow-400 font-medium">ETH</span>
                            </div>
                        </div>
                        <div className="grid grid-cols-2 gap-3 mt-6">
                            <button
                                onClick={() => setShowSellModal(false)}
                                className="w-full bg-gray-700 hover:bg-gray-600 text-white py-2 rounded-md"
                            >
                                CANCEL
                            </button>
                            <button
                                onClick={() => handleSellMiner()}
                                className="w-full bg-gradient-to-r from-yellow-500 to-orange-400 hover:opacity-90 text-[#78350F] font-bold py-2 rounded-md"
                            >
                                {isListingMiner ? 'Listing...' : 'List Miner'}
                            </button>
                        </div>
                    </div>
                </div>
            )}
        </div>
    )
}
